import * as utf8 from 'utf8';
import { configureHandler, DEFAULT_WRAPPER, initWasmModule, wrap, WrappedVigenereCipherWasmModule } from '../wasm/vigenereCipher';
import KEY from './CipherKey';
import { createDeferred } from './Deferred';

/* tslint:disable no-bitwise */

/**
 * The codec is designed to consume UTF-8 characters.
 */

const deferred = createDeferred<void>();

function encodeByte(byte: number, index: number) {
    if (byte < 32 || byte >= 127) {
        return byte;
    }
    let encodedByte = byte + (KEY[index % 512] - 32);
    if (encodedByte >= 127) {
        // encodedByte = 32 + (decodedByte - 127);
        encodedByte = encodedByte - 95;
    }
    return encodedByte & 0xFF;
}

function decodeByte(byte: number, byteIndex: number) {
    if (byte < 32 || byte >= 127) {
        return byte;
    }
    let decodedByte = byte - (KEY[byteIndex % 512] - 32);
    if (decodedByte < 32) {
        // decodedByte = 127 - (32 - decodedByte);
        decodedByte = 95 + decodedByte;
    }
    return decodedByte;
}

interface Handler {
    onerror(msg: string): void;
}
export interface IVigenereCipher {
    onWasmLoaded: Promise<void>;
    wasmLoaded: boolean;
    encrypt(plain: string): string;
    decrypt(cipher: string): string;
    encryptBuffer(buffer: ArrayBuffer | SharedArrayBuffer, offset?: number, count?: number): ArrayBuffer | SharedArrayBuffer;
    decryptBuffer(buffer: ArrayBuffer | SharedArrayBuffer, offset?: number, count?: number): ArrayBuffer | SharedArrayBuffer;
    wasmModule: WrappedVigenereCipherWasmModule;
    configureHandler(handler?: Handler): void;
}

const VigenereCipher: IVigenereCipher = {
    encrypt(plain: string): string {
        const utf8String = utf8.encode(plain);
        let cipher = '';
        for (let i = 0; i < utf8String.length; i++) {
            const low = utf8String.charCodeAt(i) & 0xFF;
            const encodedLowByte = encodeByte(low, i);
            cipher += String.fromCharCode(encodedLowByte);
        }
        return utf8.decode(cipher);
    },

    decrypt(cipher: string): string {
        const utf8Cipher = utf8.encode(cipher);
        let plain = '';
        for (let i = 0; i < utf8Cipher.length; i++) {
            const char = utf8Cipher[i];
            // const {low, high} = getCharByteCode(char);
            const low = char.charCodeAt(0) & 0xFF;
            const decodedLowByte = decodeByte(low, i);
            plain += String.fromCharCode(decodedLowByte);
        }
        return utf8.decode(plain);
    },

    encryptBuffer(buffer: ArrayBuffer | SharedArrayBuffer, offset: number = 0, count?: number) {
        const input = new Uint8Array(buffer, offset, count);
        const output = new Uint8Array(input.length);
        for (let i = 0; i < input.length; i++) {
            output[i] = encodeByte(input[i], i);
        }

        return output.buffer;
    },

    decryptBuffer(buffer: ArrayBuffer | SharedArrayBuffer, offset: number = 0, count?: number) {
        const input = new Uint8Array(buffer, offset, count);
        const output = new Uint8Array(input.length);
        for (let i = 0; i < input.length; i++) {
            output[i] = decodeByte(input[i], i);
        }

        return output.buffer;
    },
    configureHandler(handler) {
        configureHandler(handler);
    },
    onWasmLoaded: deferred.promise,
    wasmLoaded: false,
    wasmModule: DEFAULT_WRAPPER
};

function initializeWasm() {
    try {
        const wasmVigenereCipher = initWasmModule({
            onAbort(reason: any) {
                console.warn(`wasm initialize failed, reason:${reason}, fall back`);
                deferred.reject(reason);
            },
        });

        wasmVigenereCipher.then(function (wasm) {
            const keyBuffer = wasm._getKeyBuffer();
            const wrappedModule = wrap(wasm);
            const { unpackToString, packString, decrypt, encrypt, unpackToBuffer, packBuffer } = wrappedModule;

            wasm.writeArrayToMemory(new Uint8Array(KEY), keyBuffer);

            VigenereCipher.wasmModule = wrappedModule;

            VigenereCipher.encrypt = function (plain: string) {
                return unpackToString(encrypt(packString(plain)));
            };

            VigenereCipher.decrypt = function (cipher: string) {
                return unpackToString(decrypt(packString(cipher)));
            };

            VigenereCipher.encryptBuffer = function (buffer: ArrayBuffer | SharedArrayBuffer, offset: number = 0, count?: number) {
                return unpackToBuffer(encrypt(packBuffer(buffer, offset, count)));
            };

            VigenereCipher.decryptBuffer = function (buffer: ArrayBuffer | SharedArrayBuffer, offset: number = 0, count?: number) {
                return unpackToBuffer(decrypt(packBuffer(buffer, offset, count)));
            };

            if (CONFIG.IS_TESTING) {
                (window as any).wasmEncrypt = VigenereCipher.encrypt;
                (window as any).wasmDecrypt = VigenereCipher.decrypt;
                (window as any).wasmEncryptBuffer = VigenereCipher.encryptBuffer;
                (window as any).wasmDecryptBuffer = VigenereCipher.decryptBuffer;
            }

            deferred.resolve();
            VigenereCipher.wasmLoaded = true;
        });
    } catch (error) {
        console.warn('wasm initialize failed, fall back');
        deferred.reject(error);
    }
}

if (CONFIG.IS_DEV_OR_TESTING) {
    (window as any).jsEncrypt = VigenereCipher.encrypt;
    (window as any).jsDecrypt = VigenereCipher.decrypt;
    (window as any).jsEncryptBuffer = VigenereCipher.encryptBuffer;
    (window as any).jsDecryptBuffer = VigenereCipher.decryptBuffer;
    (window as any).VigenereCipher = VigenereCipher;
}

initializeWasm();

export default VigenereCipher;
