import * as utf8 from 'utf8';
import { VigenereCipherWasmModule, WrappedVigenereCipherWasmModule } from './types';

function wasmNotLoaded(...args: any[]): never {
    throw new Error('wasm not loaded!');
}

const DEFAULT_HANDLER = {
    onerror: (msg: string) => {
        console.error(new Error(msg));
    }
};

const handler = DEFAULT_HANDLER;

export function configureHandler(value?: Partial<typeof DEFAULT_HANDLER>) {
    handler.onerror = value?.onerror ?? DEFAULT_HANDLER.onerror;
}

const SIZE_FIELD_OFFSET = 4;
const DATA_FILE_OFFSET = 8;

const utf8Decoder = typeof TextDecoder !== 'undefined' ? new TextDecoder('utf8') : undefined;
const utf8Encoder = typeof TextEncoder !== 'undefined' ? new TextEncoder() : undefined;

function UTF8ArrayToString(u8Array: Uint8Array, begin: number, end?: number) {
    const subarray = u8Array.subarray(begin, end);
    if (utf8Decoder) {
        return utf8Decoder.decode(subarray);
    } else if (typeof Buffer !== 'undefined') {
        return new Buffer(subarray).toString('utf-8');
    } else {
        return utf8.decode(subarray.reduce<string>((acc, next) => acc + String.fromCharCode(next), ''));
    }
}

function stringToUTF8Array(str: string, u8Array: Uint8Array, begin: number, end?: number) {
    const subarray = u8Array.subarray(begin, end);

    if (typeof str !== 'string') {
        subarray.fill(0);
        handler.onerror('Invalid input: input not a string, cannot encode');
        return;
    }

    if (utf8Encoder) {
        if ((utf8Encoder as any).encodeInto) {
            (utf8Encoder as any).encodeInto(str, subarray);
        } else {
            const data = utf8Encoder.encode(str);
            subarray.set(data);
        }
    } else if (typeof Buffer !== 'undefined') {
        const buffer = Buffer.from(str, 'utf-8');
        buffer.copy(subarray as any);
    } else {
        const data = utf8.encode(str);
        for (let i = 0; i < subarray.length; i++) {
            subarray[i] = data.charCodeAt(i);
        }
    }
}

export const DEFAULT_WRAPPER: WrappedVigenereCipherWasmModule = {
    get rawModule() {
        return wasmNotLoaded();
    },
    encrypt: wasmNotLoaded,
    decrypt: wasmNotLoaded,

    compress: wasmNotLoaded,
    decompress: wasmNotLoaded,

    base64Encode: wasmNotLoaded,
    base64Decode: wasmNotLoaded,

    packString: wasmNotLoaded,
    packBuffer: wasmNotLoaded,

    unpackToBuffer: wasmNotLoaded,
    unpackToString: wasmNotLoaded
};

export default function wrap(wasmModule: VigenereCipherWasmModule): WrappedVigenereCipherWasmModule {

    return {
        rawModule: wasmModule,

        encrypt(ptr: number) {
            return wasmModule._vigenereEncrypt(ptr);
        },
        decrypt(ptr: number) {
            return wasmModule._vigenereDecrypt(ptr);
        },
        compress(ptr: number) {
            const result = wasmModule._gzipCompress(ptr);
            wasmModule._freeBuffer(ptr);

            return result;
        },
        decompress(ptr: number) {
            try {
                const result = wasmModule._gzipDecompress(ptr);
                return result;
            } catch (err) {
                const error = new Error('Invalid data: decompress failed');
                (error as any).internal = err;
                throw error;
            } finally {
                wasmModule._freeBuffer(ptr);
            }
        },
        base64Encode(ptr: number) {
            const result = wasmModule._base64Encode(ptr);
            wasmModule._freeBuffer(ptr);

            return result;
        },
        base64Decode(ptr: number) {
            const result = wasmModule._base64Decode(ptr);
            wasmModule._freeBuffer(ptr);
            return result;
        },
        packString(str: string) {
            const length = wasmModule.lengthBytesUTF8(str);
            const buffer = wasmModule._createBuffer(length);
            const data = wasmModule.getValue(buffer + DATA_FILE_OFFSET, 'i32*');
            stringToUTF8Array(str, wasmModule.HEAPU8, data, data + length);
            return buffer;
        },
        packBuffer(buffer: ArrayBuffer | SharedArrayBuffer, offset: number = 0, count?: number) {
            const uint8Array = new Uint8Array(buffer, offset, count);
            const dataBuffer = wasmModule._createBuffer(uint8Array.length);
            const data = wasmModule.getValue(dataBuffer + DATA_FILE_OFFSET, 'i32*');

            wasmModule.writeArrayToMemory(uint8Array, data);

            return dataBuffer;
        },
        unpackToString(ptr: number) {
            const length = wasmModule.getValue(ptr + SIZE_FIELD_OFFSET, 'i32');
            const data = wasmModule.getValue(ptr + DATA_FILE_OFFSET, 'i32*');
            const result = UTF8ArrayToString(wasmModule.HEAPU8, data, data + length);
            wasmModule._freeBuffer(ptr);

            return result;
        },
        unpackToBuffer(ptr: number) {
            const length = wasmModule.getValue(ptr + SIZE_FIELD_OFFSET, 'i32');
            const data = wasmModule.getValue(ptr + DATA_FILE_OFFSET, 'i32*');
            const result = wasmModule.HEAPU8.slice(data, data + length).buffer;

            wasmModule._free(ptr);

            return result;
        }
    };
}
