<template>
  <div class="container">
    <div class="img-content">
      <el-image
        v-for="(item, index) in renderList"
        :key="index"
        class="img"
        :src="item.imgUrl"
        fit="fill"
      ></el-image>
      <el-button
        v-for="item in imgList"
        @click="imgBtnClick(item.id)"
        :key="item.id"
        style="position: relative"
      >
        {{ item.name }}
      </el-button>
    </div>

    <div class="container">
      <div class="btn-content"></div>
    </div>
  </div>
</template>

<script>
import { decode } from "@/utils/encrypted";
export default {
  name: "TestView",
  data() {
    return {
      renderList: [],
      imgList: [],
      scenarios: [],
    };
  },
  created() {
    this.testScenarioMode();
  },
  methods: {
    imgBtnClick(id) {
      const searchRes = this.renderList.filter((img) => img.id === id);
      if (searchRes.length !== 0) {
        this.imgRemove(id);
        return;
      }
      this.renderList.push(this.imgList.filter((img) => img.id === id)[0]);
    },
    imgRemove(id) {
      this.renderList = this.renderList.filter((img) => img.id !== id);
    },
    async testScenarioMode() {
      let requestTest = this.$axios.create({
        headers: {
          "x-tool-name": "diy",
          "x-qh-locale": "zh_CN",
        },
      });
      const res = await requestTest.get(
        "/klt-scenemode/api/scene/scenelayerlist?picid=3FO7KF623KTM&taskid=L3D187S23ENDPHFW4PQUWJTX4LUF3P3XC888&isset=false"
      );
      const decodeData = decode(res.data);
      this.imgList = decodeData.lightLayers;
      this.scenarios = decodeData.sceneRenderPics;
      console.log(this.imgList, this.scenarios);
    },
  },
};
</script>

<style>
.container {
  display: flex;
  justify-content: center;
  width: 100%;
}
.img-content {
  width: 100%;
  position: relative;
}

.img {
  width: 1024px;
  position: absolute;
}

.img:nth-child(2) {
  mix-blend-mode: screen; /* screen */
}
</style>
