import VigenereCipher from "@qunhe/tools-common/lang/VigenereCipher";

function encode(data) {
  if (data !== undefined) {
    return VigenereCipher.encrypt(
      typeof data === "string" ? data : JSON.stringify(data)
    );
  }
  return data;
}

function decode(data) {
  const string = VigenereCipher.decrypt(data);
  if (string) {
    // string may be ""
    return JSON.parse(string);
  }
  return string;
}

export { encode, decode };
